import React, { Component } from 'react';

export default class Tabs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: 'local',
      isHoverShare: false
    };
  }
  hoverShare(){
    this.setState({
      isHoverShare: !this.state.isHoverShare
    });
  }
  selectTab(name) {
    this.setState({
      active: name
    });
  }
  activeTab(name) {
    return this.state.active === name;
  }
  render() {
    return <div className="tabs">
    <div className="tabs-nav">
      <div className={`tab ${this.activeTab('local') ? 'active' : ''}`} onClick={()=>this.selectTab('local')}>Local</div>
      <div className={`tab ${this.activeTab('international') ? 'active' : ''}`} onClick={()=>this.selectTab('international')}>International</div>
    </div>
    <span 
      onMouseEnter={this.hoverShare.bind(this)}
      onMouseLeave={this.hoverShare.bind(this)}
      className={`share ${!this.state.isHoverShare ? 'icon-share-ic' : 'icon-share-ic-hover'}`}></span>
    <div id="tab1" className={`tab-content ${this.activeTab('local') ? 'active' : ''}`}>
      <div className="local-table">
        <div className="local-table--row">
          <div className="local-table--col">
            <div className="local-label">BIC</div>
            <div className="local-text">REVO4758</div>
          </div>
          <div className="local-table--col">
            <div className="local-label">Beneficiary</div>
            <div className="local-text">Andrew Lucas</div>
          </div>
        </div>
        <div className="local-table--row">
          <div className="local-table--col">
            <div className="local-label">IBAN</div>
            <div className="local-text">7577 REVO 4758 3957 5893 75 </div>
          </div>
          <div className="local-table--col">
            <div className="local-label">Address</div>
            <div className="local-text">Level 37, One Canada Square, Canary Wharf, London E14 5AB</div>
          </div>
        </div>
      </div>
    </div>
    <div id="tab2" className={`tab-content ${this.activeTab('international') ? 'active' : ''}`}>
      <div className="local-table">
        <div className="local-table--row">
          <div className="local-table--col">
            <div className="local-label">BIC</div>
            <div className="local-text">REV1234322</div>
          </div>
          <div className="local-table--col">
            <div className="local-label">Beneficiary</div>
            <div className="local-text">Andrew Lucas</div>
          </div>
        </div>
        <div className="local-table--row">
          <div className="local-table--col">
            <div className="local-label">IBAN</div>
            <div className="local-text">1231 REVO 4322 3957 5893 75 </div>
          </div>
          <div className="local-table--col">
            <div className="local-label">Address</div>
            <div className="local-text">Level 37, One Canada Square, Canary Wharf, London E14 5AB</div>
          </div>
        </div>
      </div>
    </div>
  </div>;
  }
}
